<template>
  <div id="general-page">
    <div id="myChart" :style="{width: '100%', height: '100%'}"></div>
  </div>
</template>

<script>
export default {
  mounted() {
    this.drawLine();
  },
  methods: {
    drawLine() {
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      myChart.setOption({
        series: {
                type: 'pie',
                data: [
                    {name: 'A', value: 12102},
                    {name: 'B', value: 2323},
                    {name: 'C', value: 1919}
                ]
            }
      });
    }
  }
};
</script>

<style>
#general-page{
    height: 100%;
    width: 100%;
}
</style>